@font-face {
  font-family: "RobotoCondensed";
  font-weight: 400;
  font-style: normal;
  src: url("../fonts/RobotoCondensed-Regular.woff2") format("woff2"), url("../fonts/RobotoCondensed-Regular.woff") format("woff"), url("../fonts/RobotoCondensed-Regular.ttf") format("ttf"); }

@font-face {
  font-family: "RobotoCondensed";
  font-weight: 700;
  font-style: normal;
  src: url("../fonts/RobotoCondensed-Bold.woff2") format("woff2"), url("../fonts/RobotoCondensed-Bold.woff") format("woff"), url("../fonts/RobotoCondensed-Bold.ttf") format("ttf"); }

@font-face {
  font-family: "PTMono";
  font-weight: 400;
  font-style: normal;
  src: url("../fonts/PTMono-Regular.woff2") format("woff2"), url("../fonts/PTMono-Regular.woff") format("woff"), url("../fonts/PTMono-Regular.ttf") format("ttf"); }

body {
  margin: 0 auto;
  padding: 0;
  font-family: "PTMono", "Arial", sans-serif; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

.mobile-only {
  display: none; }

.tablet-only {
  display: none; }

.desktop-only {
  display: block; }

@media (max-width: 1023px) {
  .mobile-only {
    display: none; }
  .tablet-only {
    display: block; }
  .desktop-only {
    display: none; } }

@media (max-width: 767px) {
  .mobile-only {
    display: block; }
  .tablet-only {
    display: none; }
  .desktop-only {
    display: none; } }

.container {
  max-width: 1440px;
  margin: 0 auto;
  padding: 0 15px;
  box-sizing: border-box; }

.button {
  padding: 15px 27px 13px;
  display: block;
  box-sizing: border-box;
  background-color: transparent;
  border: none;
  outline: none;
  border-radius: 5px;
  color: #ffffff;
  text-transform: uppercase;
  font-family: "RobotoCondensed", "Arial", sans-serif;
  font-weight: 700;
  font-size: 14px;
  line-height: 16px;
  cursor: pointer;
  letter-spacing: 0.05em; }

.page-header {
  background-color: #1B2D37;
  width: 100%;
  position: absolute;
  top: 0;
  left: 0; }

.page-header__content {
  display: flex;
  justify-content: space-between;
  padding: 33px 0; }

.page-header__contacts {
  display: flex;
  align-items: center; }

.page-header__link {
  display: flex;
  align-items: center;
  text-decoration: none;
  color: #ffffff;
  font-size: 16px;
  line-height: 18px;
  height: 100%; }
  .page-header__link svg {
    fill: #00b2ff;
    display: flex;
    margin-right: 20px; }

.page-header__email {
  margin-right: 88px; }

.page-header__tel {
  margin-right: 48px; }
  .page-header__tel svg {
    margin-right: 17px; }

.page-header__callback {
  border: 1px solid #ffffff; }

@media (max-width: 1023px) {
  .page-header__contacts {
    position: relative;
    top: -4px;
    left: 1px; }
  .page-header__link svg {
    margin: 0; }
  .page-header__link use {
    width: 100%;
    height: 100%; }
  .page-header__link path {
    width: 100%;
    height: 100%; }
  .page-header__link span {
    display: none; }
  .page-header__email {
    margin-right: 33px; }
    .page-header__email svg {
      width: 25px;
      height: 19px; }
  .page-header__tel {
    margin-right: 20px; }
    .page-header__tel svg {
      width: 24px;
      height: 24px; }
  .page-header__callback {
    display: none; } }

@media (max-width: 767px) {
  .page-header__content {
    padding: 16px 0; }
  .page-header__logo-big {
    display: none; }
  .page-header__logo-mobile {
    display: block;
    padding-left: 2px; }
  .page-header__contacts {
    top: -2px;
    left: 0; }
  .page-header__link {
    margin-right: 0; }
  .page-header__email {
    margin-right: 24px; } }
